.login-container {
  width: 70%;
  margin: 3rem auto;
  margin-top: 100px;
  border: 1px solid;
  border-radius: 30px;
  background-color: #f2f2f2;
}
.login-text {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
}
.login-container button {
  width: 250px;
  height: 50px;
  color: white;
  background-color: black;
  margin: 40px 5px 40px 5px;
  font-size: 30px;
}

.login-container label {
  display: inline-block;
  width: 130px;
  font-size: 25px;

  text-align: right;
}
.login-container input {
  width: 250px;
  height: 40px;
  margin-left: 20px;
}
