.register-container {
  width: 80%;
  border: 1px solid;
  border-radius: 50px;
  margin: 3rem auto;
  background-color: #f2f2f2;
}
.check-button {
  margin-left: 30px;
  color: white;
  background-color: black;
  width: 100px;
}
.register-button {
  width: 250px;
  height: 50px;
  color: white;
  background-color: black;
  margin: 10px auto;
  margin-top: -40px;
  font-size: 30px;
}
.register-container label {
  display: inline-block;
  float: left;
  clear: left;
  width: 130px;
  font-size: larger;
  font-weight: bold;
  text-align: right;
}
.register-container input {
  width: 200px;
  margin-left: 50px;
}
